import React, { useState, useEffect, useRef } from 'react'
import { Illustration } from 'react-zdog'
import { SVGStyles } from './styles'
import Head from './atoms/head'
import { AppContext, defaultContext } from './context'
import c from './config'

/**
 * View
 * @typedef FunctionComponent<{zoom:number}>
 * @description from that part the component is written in JSX 
 * because react-zdog still not support TypeScript
 */
const App = ({ zoom = 1 }) => {
  /**
   * @typedef typeof defaultContext.focus
   */
  const [focusCoord, setFocusCoord] = useState(defaultContext.focus)

  /**
   * @typedef typeof defaultContext
   */
  const contextValues = { 
    focus: focusCoord
  }

  /**
   * From mouse move to app focus coordinates
   * @typedef (MouseEvent) => void
   */
  const handleMouseMove = ({ clientX, clientY }) => {
    setFocusCoord({
      x: Math.round(((200 / window.innerWidth) * clientX) - 100) / 100,
      y: Math.round(((200 / window.innerHeight) * clientY) - 100) / 100
    })
  }

  /**
   * Manage events and init values
   */
  useEffect(() => {
    window.addEventListener('mousemove', handleMouseMove)
    
    return () => window.removeEventListener('mousemove', handleMouseMove)
  }, [])

  /**
   * Render
   */
  return (
    <AppContext.Provider value={contextValues}>
      <Illustration 
        zoom={zoom} 
        dragRotate 
        rotate={{ x: 0, y: 0 }}
      >
        <Head /> 
      </Illustration>
      <SVGStyles />
    </AppContext.Provider>
  )
}

export default App