
import React, { FunctionComponent, useState, useEffect, useRef } from 'react'
import App from './app'
import c from './config'

/**
 * Config
 */
const illuContentSize = c.skull.height + (c.jaw.size / 2)

/**
 * Styles
 */
const containerStyle:Record<string, string|number> = { position: 'relative', width: '100%', paddingTop: '100%' }
const wrapperStyle:Record<string, string|number> = { position: 'absolute', width: '100%', height: '100%', top: 0, left: 0 }

/**
 * View
 */
const TheeDAvatar:FunctionComponent = () => {
  /**
   * States & refs
   */
  const [zoom, setZoom] = useState<number>(1)
  const illuRef = useRef<HTMLDivElement>(null)

  /**
   * Handle zoom
   */
  const handleZoom = () => {
    if (!illuRef.current) return

    let bounding = illuRef.current.getBoundingClientRect()
    let viewportSize = Math.min(bounding.width, bounding.height)
    let ratio = viewportSize < 500 ? 0.8 : 0.7

    setZoom((viewportSize / illuContentSize) * ratio)
  }

  /**
   * Init zoom
   */
  useEffect(handleZoom, [illuRef])

  /**
   * Events
   */
  useEffect(() => {
    window.addEventListener('resize', handleZoom)
    return () => window.removeEventListener('resize', handleZoom)
  }, [])

  /**
   * Render
   */
  return (
    <div style={containerStyle}>
      <div ref={illuRef} style={wrapperStyle}>
        <App zoom={zoom} />
      </div>
    </div>
  )
}

export default TheeDAvatar