import React, { useEffect } from 'react'
import {  Shape, Anchor } from 'react-zdog'
import { useSpring, a, config } from '@react-spring/zdog'
import c from '../config'

const xBase =  c.eyes.shift / 2
const yBase = -(c.skull.height * c.ears.position) - c.eyebow.high
const zBase = (c.skull.width / 2) + (c.eyebow.thick / 2)
const baseDelay = 5000
const baseMov = c.eyebow.high * 0.25

export function EyeBow ({ invert, ...extra }) {
  const direction = invert ? -1 : 1

  const [{ t }, api] = useSpring(() => ({ 
    from: { t: 0 },
    to: { t: 0 },
    config: {
      ...config.gentle,
      easing: x => x * x * x * x * x
    }
  }))

  const handleMove = () => api.start({
    to: [
      { t: 1 },
      { t: 0 }
    ]}
  )

  useEffect(() => {
    let timer = setInterval(() => {
      let r = Math.random()
      if ((invert && r < 0.2) || (!invert && r < 0.5)) {
        handleMove()
      } 
    }, baseDelay);

    return () => clearInterval(timer)
  }, [])

  return (
    <Anchor {...extra}>
      <a.Shape 
        visible={false}
        translate={t.to(t => ({
          y: -baseMov * t,
          x: 3 * (invert ? t : -t)
        }))}
        rotate={t.to(t => ({
          z: (Math.PI / 30) * t * (invert ? -1: 1)
        }))}
      >
        <Shape 
          path={[
            {
              x: direction * xBase, 
              y: yBase,
              z: zBase
            }, { 
              arc: [{
                x: direction * (xBase + (c.eyebow.length / 2)), 
                y: yBase - c.eyebow.curve,
                z: zBase
              }, {
                x: direction * (xBase + c.eyebow.length), 
                y: yBase + c.eyebow.decline,
                z: zBase
              }]
            }
          ]}
          stroke={4}
          closed={false}
          color={"url(#eyeBow)"}
        />
      </a.Shape>
    </Anchor>
  )
} 