

import React, { useState, useEffect, useContext } from 'react'
import { Shape } from 'react-zdog'
import { useSpring, a, config } from '@react-spring/zdog'
import { AppContext } from '../context'
import c from '../config'
import { Skull } from './skull'
import { Ear, EarRing } from './ear'
import { Eye } from './eye'
import { EyeBow } from './eyebow'
import { Glasses } from './glasses'
import { Jaw } from './jaw'
import { Nose } from './nose'

const baseRotationX = Math.PI / 8
const baseRotationY = Math.PI / 12
const baseMov = Math.PI / 5000
const amplitude = c.skull.width * 2

const mov = d => ({ 
  x: Math.round(amplitude * Math.sin(7 * Math.PI * d )) * baseMov,
  y: Math.round(amplitude * Math.cos(5 * Math.PI * d )) * baseMov,
})

/**
 * Adjust position of the group
 * Because the skull and the jaw don't have the same size
 * but both are centered on the center of the illustration
 */
const adjustHeadPos = {
  y: c.skull.height - (c.skull.height + (c.jaw.size / 2)) / 2
}

/**
 * HEAD
 */
export default function Head () {
  const [toggle, setToggle] = useState(true)
  const { focus } = useContext(AppContext)

  /**
   * Focus animation
   */
  const [{ pos }, api] = useSpring(() => ({
    pos: [0, 0]
  }))  

  /**
   * Global move animation
   */
  const { t } = useSpring({
    from: { t: 0 },
    to: [
      { t: 1 },
      { t: 0 }
    ],
    config: { duration: 10000 }
  })

  /**
   * Handle focus animation
   */
  useEffect(() => {
    // rotation fit the opposite values than translation!
    api.start({ pos: [-focus.y * baseRotationY, -focus.x * baseRotationX] })
  }, [api, focus])


  /**
   * Render
   */
  return (
    <a.Anchor rotate={pos.to((x, y) => ({ x, y }))}>
      <a.Anchor rotate={t.to(mov)}>
        <Shape visible={false} translate={adjustHeadPos}>
          {/* skull */}
          <Skull>
            {/* glasses */}          
            <Glasses />
            
            {/* nose */}
            <Nose />
            
            {/* eyes */}          
            <Eye />
            <Eye invert />

            {/* eyebows */}          
            <EyeBow />
            <EyeBow invert />
              
            {/* ears */}          
            <Ear>
              <EarRing />
            </Ear>
            <Ear invert />
          </Skull>
          
          {/* Jaw */}
          <Jaw />
        </Shape>
      </a.Anchor>
    </a.Anchor>
  )
}