import React, { useEffect, useContext } from 'react'
import { 
  Ellipse, 
  Shape, 
  RoundedRect, 
  Hemisphere,
  Cylinder, 
  Anchor, 
  Group
} from 'react-zdog'
import { AppContext } from '../context'
import { useSpring, a } from '@react-spring/zdog'
import c from '../config'


export function Skull ({ children, ...extra }) {
  const cylinderHeight = c.skull.height - (c.skull.width / 2)

  return (
    <Anchor {...extra}>
      <Shape 
        visible={false} 
        translate={{
          x: c.skull.width * 0.01
        }}
        rotate={{ 
          y: 0.05,
          x: -0.08, 
          z: -0.01, 
        }}
      >
        <Cylinder 
          diameter={c.skull.width}
          length={cylinderHeight}
          color={c.details.skinColor}
          backface={"url(#interiorSkin)"}
          translate={{ y: -(cylinderHeight / 2) - 1}}
          rotate={{ x: Math.PI / 2 }}
          stroke={false}
        />
        <Hemisphere 
          diameter={c.skull.width}
          color={c.details.skinColor}
          stroke={false}
          translate={{ y: -cylinderHeight}}
          rotate={{ x: Math.PI / 2 }}
        />
        {children}
      </Shape>
    </Anchor>
  )
}
