import React, { useEffect, useContext } from 'react'
import { Ellipse, Anchor, Group } from 'react-zdog'
import { AppContext } from '../context'
import { useSpring, a } from '@react-spring/zdog'
import c from '../config'

const zBase = c.skull.width / 2 
const yBase = -(c.skull.height * c.ears.position)
const baseMovementX = c.eyes.width / 6
const baseMovementY = c.eyes.height / 3

export function Eye ({ invert, ...extra }) {
  /**
   * Context
   */
  const { focus } = useContext(AppContext)

  /**
   * Animation
   */
  const [{ pos }, api] = useSpring(() => ({ pos: [0, 0] }))  

  /**
   * Handle animation
   */
  useEffect(() => {
    let direction = Math.round((focus.x + focus.y) * 10) > 0 ? 1 : -1
    api.start({ pos: [
      direction * focus.x * baseMovementX, 
      direction * focus.y * baseMovementY
    ] })
  }, [api, focus])

  /**
   * Render
   */
  return (
    <Anchor {...extra}>
      <Group translate={{
        x: (invert ? -1 : 1) * (c.eyes.shift + c.eyes.width) / 2,
        y: yBase,
        z: zBase
      }}>
        {/* shape */}
        <Ellipse
          width={c.eyes.width}
          height={c.eyes.height}
          fill={true}
          color={c.eyes.colorExt}
        />
        <a.Shape 
          visible={false}
          translate={pos.to((x, y) => ({ x, y }))}
        >
          {/* iris */}
          <Ellipse
            diameter={c.eyes.height * 0.6}
            fill={true}
            color={"url(#eyeIris)"}
            translate={{ z: 0.5}}
          />
          {/* pupil */}
          <Ellipse
            diameter={c.eyes.height * 0.25}
            fill={true}
            color="#000000"
            translate={{ z: 1}}
          />
        </a.Shape>
        {/* reflect */}
        <Ellipse
          diameter={c.eyes.height * 0.25}
          fill={true}
          color="#FFFFFFC0"
          translate={{ x: c.eyes.height * 0.25, y: -c.eyes.height * 0.25, z: 1}}
        />
      </Group>
    </Anchor>
  )
}