import React, { useEffect, useContext } from 'react'
import { 
  Ellipse, 
  Shape, 
  RoundedRect, 
  Hemisphere,
  Cylinder, 
  Anchor, 
  Group
} from 'react-zdog'
import { AppContext } from '../context'
import { useSpring, a } from '@react-spring/zdog'
import c from '../config'

export function Ear ({ children, invert, ...extra }) {
  return (
    <Anchor {...extra}>
      <Ellipse 
        diameter={c.ears.height}
        quarters={2}
        stroke={c.ears.thick}
        color={c.details.skinColor2}
        rotate={invert && { y: Math.PI }}
        translate={{
          x: (invert ? -1 : 1) * (c.skull.width / 2),
          y: -(c.skull.height * c.ears.position) + (c.ears.height / 2) + (c.ears.thick / 2)
        }}
      >
        {children}
      </Ellipse>
    </Anchor>
  )
}

export function EarRing ({ ...extra }) {

  return (
    <Anchor {...extra}>
      <Ellipse 
        diameter={c.ears.thick * 0.5}
        color={'#000'}
        fill={true}
        stroke={false}
        translate={{ 
          x: Math.round(c.ears.thick / 3),
          y: c.ears.height / 2,
          z: c.ears.thick / 2
        }}
      />
      <Ellipse 
        diameter={c.ears.thick * 0.5}
        color={'#000'}
        fill={true}
        stroke={false}
        translate={{ 
          x: Math.round(c.ears.thick / 3),
          y: c.ears.height / 2,
          z: c.ears.thick / -2
        }}
      />
    </Anchor>
  )
}