/**
 * Format list of posts
 * Filter empty data
 */
export const formatPosts = (posts:INode[]):IPost[] => posts.filter(data => !!data).map(post => formatPost(post))

/**
 * Format post
 * turn data from graphQL into a friendly and safe object
 */
export const formatPost = ( data:INode ):IPost => ({
  id: data.id,
  title: data.frontmatter?.title,
  date: data.frontmatter?.date,
  category: data.frontmatter?.category,
  link: data.fields?.slug || data.slug,
  excerpt: data.excerpt,
  body: data.body,
  words: data.wordCount.words || 0,

  cover: data.frontmatter?.cover?.childImageSharp?.gatsbyImageData || undefined,
  coverCopy: data.frontmatter?.coverCopy || undefined
})