import React, { useEffect, useContext } from 'react'
import { 
  Ellipse, 
  Shape, 
  RoundedRect, 
  Hemisphere,
  Cylinder, 
  Anchor, 
  Group
} from 'react-zdog'
import { AppContext } from '../context'
import { useSpring, a } from '@react-spring/zdog'
import c from '../config'

export function Jaw ({ children, ...extra }) {
  const baseRotate = Math.PI / -2
  return (
    <a.Anchor {...extra}>
      {children}
      <Hemisphere 
        diameter={c.jaw.size}
        color={c.details.skinColor}
        backface={"url(#interiorSkin)"}
        stroke={false}
        translate={{
          x: c.jaw.size * 0.01, 
          y: 4 
        }}
        rotate={{ 
          x: baseRotate - 0.1, 
          y: 0.01, 
        }}
      />
    </a.Anchor>
  )
}