import React, { useEffect, useContext } from 'react'
import { 
  Ellipse, 
  Shape, 
  RoundedRect, 
  Hemisphere,
  Cylinder, 
  Anchor, 
  Group
} from 'react-zdog'
import { AppContext } from '../context'
import { useSpring, a } from '@react-spring/zdog'
import c from '../config'

export function Nose ({ ...extra }) {
  const zBase = c.skull.width / 2
  const yBase = -c.skull.height * c.ears.position
  const stroke = Math.max(c.nose.width, c.nose.deep) / 2
  const top = { y: yBase, x: 0, z: zBase }
  const bottom = { y: yBase + c.nose.height, x: 0, z: zBase }
  const right = { y: yBase + (c.nose.height * c.nose.dropout), x: (c.nose.width - stroke) / -2, z: zBase }
  const left = { y: yBase + (c.nose.height * c.nose.dropout), x: (c.nose.width - stroke) / 2, z: zBase }
  const front = { y: yBase + (c.nose.height * c.nose.dropout), x: 0, z: c.nose.deep - (stroke / 2) + zBase }

  return (
    <Shape
      path={[
        bottom,
        right,
        top,
        left,
        bottom,
        front,
        top
      ]}
      stroke={stroke}
      color={c.details.skinColor2}
      {...extra}
    />
  )
}