import React from 'react'
import { Shape, RoundedRect, Anchor } from 'react-zdog'
import c from '../config'

export function Glasses ({ children, ...extra }) {
  const zBase = c.skull.width / 2
  const yBase = -(c.skull.height * c.ears.position)
  const glassBasePos = (c.glasses.width / 2) + (c.glasses.shift / 2)
  const glassesSide = c.glasses.width + (c.glasses.shift / 2)

  return (
    <Anchor {...extra}>
      {/* shape left */}
      <RoundedRect  
        height={c.glasses.height}
        width={c.glasses.width}
        stroke={c.glasses.thick}
        cornerRadius={c.glasses.radius}
        color={c.glasses.color}
        translate={{ 
          y: yBase,
          x: -1 * glassBasePos,
          z: zBase + c.glasses.deep
        }}
      />
      {/* shape right */}
      <RoundedRect  
        height={c.glasses.height}
        width={c.glasses.width}
        stroke={c.glasses.thick}
        cornerRadius={c.glasses.radius}
        color={c.glasses.color}
        translate={{ 
          y: yBase,
          x: glassBasePos,
          z: zBase + c.glasses.deep
        }}
      />
      {/* lens left */}
      <RoundedRect  
        height={c.glasses.height - c.glasses.thick}
        width={c.glasses.width - c.glasses.thick}
        stroke={false}
        fill={true}
        color={c.glasses.lensColor}
        cornerRadius={c.glasses.radius * ((c.glasses.height - 2 * c.glasses.thick) / c.glasses.height)}
        translate={{ 
          y: yBase ,
          x: -1 * glassBasePos,
          z: zBase + c.glasses.deep
        }}
      />
      {/* lens right */}
      <RoundedRect  
        height={c.glasses.height - c.glasses.thick}
        width={c.glasses.width - c.glasses.thick}
        stroke={false}
        fill={true}
        color={c.glasses.lensColor}
        cornerRadius={c.glasses.radius * ((c.glasses.height - 2 * c.glasses.thick) / c.glasses.height)}
        translate={{ 
          y: yBase,
          x: glassBasePos,
          z: zBase + c.glasses.deep
        }}
      />
      {/* shape between */}
      <Shape 
        path={[
          { 
            x: c.glasses.shift / -2, 
            y: yBase,
            z: zBase + c.glasses.deep 
          }, { 
            x: c.glasses.shift / 2, 
            y: yBase,
            z: zBase + c.glasses.deep 
          }
        ]}
        stroke={c.glasses.thick}
        color={c.glasses.color}
      />
      {/* branch left */}
      <Shape 
        path={[
          { 
            x: glassesSide, 
            y: yBase, 
            z: zBase + c.glasses.deep 
          }, { 
            x: c.skull.width / 2, 
            y: yBase + (c.glasses.thick / -2),
            z: zBase + (c.skull.width / -2)
          }
        ]}
        stroke={c.glasses.thick}
        color={c.glasses.color}
      />
      {/* branch right */}
      <Shape 
        key="branchRight"
        path={[
          { 
            x: -glassesSide, 
            y: yBase, 
            z: zBase + c.glasses.deep 
          }, { 
            x: -c.skull.width / 2, 
            y: yBase - (c.glasses.thick / -2), 
            z: zBase + c.skull.width / -2 
          }
        ]}
        stroke={c.glasses.thick}
        color={c.glasses.color}
      />
      {children}
    </Anchor>
  )
}